.vux-timeline{padding:1rem}.vux-timeline>ul>li{list-style:none}.vux-timeline-item{position:relative}.vux-timeline-item-content{padding:0 0 1.5rem 1.2rem}.vux-timeline-item-head,.vux-timeline-item-head-first{position:absolute;content:'';z-index:99;border-radius:99px}.vux-timeline-item-head{width:10px;height:10px;left:1px;top:4px}.vux-timeline-item-head-first{width:20px;height:20px;left:-4px;top:5px}.vux-timeline-item-tail{position:absolute;content:'';height:100%;width:2px;left:5px;top:5px;background-color:#04BE02}.vux-timeline-item-checked{width:100%;position:absolute;left:0;top:45%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.vux-timeline-item-checked::before{font-size:12px;width:20px;color:#FFF}.vux-timeline-item-color{background-color:#04BE02}


